export const statusTranslate = {
  completed: {status: 'termine'},
  historicalArchive: {status: 'archive'},
  obsolete: {status: 'obsolte', consequences: 'Les donnes peuvent tre incompltes ou ne plus tre  jour'},
  onGoing: {status: 'en cours'},
  planned: {status: 'planifie'},
  required: {status: 'mise  jour requise'},
  underDevelopment: {status: ' en construction', consequences: 'Certaines donnes peuvent tre errones ou vont subir des changements'},
}

export function isWarningStatus(status) {
  if (status) {
    if (statusTranslate[status] && statusTranslate[status].consequences) {
      return true
    }
  }
  return false
}



