import React from 'react'
import { get } from 'lodash'

import Chart from '../../../../components/Charts/Chart'
import DoughnutChart from '../../../../components/Charts/DoughnutChart/DoughnutChart'
import Counter from '../../../../components/Statistics/Counter/Counter'
import Percent from '../../../../components/Statistics/Percent/Percent'

import { container, section, chart } from './StatisticsSection.scss'

const StatisticsSection = ({metrics}) => {
  const openness = get(metrics, 'datasets.partitions.openness.yes', 0)
  const download = get(metrics, 'datasets.partitions.download.yes', 0)

  return (
    <div className={container}>
      <h2>Indicateurs concernant la totalit du catalogue</h2>
      <div className={section}>

        <div className={chart}>
          <Counter size="large" value={metrics.records.totalCount} title="Enregistrements"/>
        </div>

        <div className={chart}>
          <Chart
            description={'Rpartition des types d\'enregistrements'}
            chart={<DoughnutChart data={metrics.records.partitions.recordType} />} />
        </div>

        <div className={chart}>
          <Chart
            description={'Rpartition des types de meta donne'}
            chart={<DoughnutChart data={metrics.records.partitions.metadataType} />} />
        </div>
      </div>

      <h2>Indicateurs concernant les jeux de donnes</h2>
      <div className={section}>
        <div className={chart}>
          <Percent value={openness} total={metrics.datasets.totalCount} size="large" icon="unlock alternate icon" title="Pourcentage de donnes ouvertes" />
        </div>

        <div className={chart}>
          <Percent value={download} total={metrics.datasets.totalCount} size="large" icon="download" title="Pourcentage de jeu de donnes tlchargeable" />
        </div>

        <div className={chart}>
          <Chart
            description={'Rpartition des types de donne'}
            chart={<DoughnutChart data={metrics.datasets.partitions.dataType} />} />
        </div>
      </div>
    </div>
  )
}

export default StatisticsSection



